.singleProductDetails {
  .product {
    margin-top: 36px;
    display: flex;
    align-items: center;
    background-color: #fff;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    border-radius: 10px;
    padding-right: 20px;
    gap: 6%;
    padding-top: 25px;
    padding-bottom: 25px;


    .images {
      width: 45%;

      .mainImage {

        img {
          width: 100%;
          max-height: 65vh;
          object-fit: contain;
        }
      }

      .smallImages {
        display: flex;
        gap: 6px;
        align-items: center;
        margin-left: 30px;

        img {
          width: 220px;
          max-height: 183px;
          object-fit: cover;
          background-color: #f4ecec;
          padding: 8px;
          border-radius: 6px;
          cursor: pointer;
          border: 1px solid #bfb2b2;
        }
      }

    }

    .descriptions {
      width: 64%;

      .title {
        font-size: 23px;
        font-weight: 600;
        color: black;
        line-height: 40px;
      }

      p.desc {
        width: 65%;
      }

      .desc {
        font-size: 19px;
        font-weight: 400;
        color: #989696;
        line-height: 40px;
      }

      .desc {
        display: flex;
        align-items: center;
        gap: 20px;

        p {
          color: rgb(76, 75, 75);
          font-weight: 400;

          font-size: 19px;

          span {
            color: #ff5722;
            font-weight: 400;
            border-left: 1px solid #ff5722;
            padding-left: 20px;
            font-size: 19px;


          }

          .firstSpan {
            border: 0;
            padding-left: 0;
          }

        }



      }

      .price {
        background-color: #e7e1e1;
        margin-top: 10px;
        padding: 22px 11px;
        border-radius: 8px;

        .taxes {
          font-size: 20px;
          color: #5a5a5a;
          font-weight: 400;
        }

        .priceBrforeDesc {
          font-size: 20px;
          color: grey;
          margin-right: 13px;
          line-height: 50px;
        }

        .disc {
          display: flex;
          gap: 15px;
          align-items: center;


          .priceWithDesc {
            color: #ff5722;
            font-size: 40px;
            font-weight: 800;
          }

          .discount {
            background-color: #ff5722;
            color: #fff;
            padding: 8px;
            font-size: 22px;
            font-weight: 700;
            border-radius: 7px;
          }
        }
      }

      .quantityOfProduct {
        display: flex;
        align-items: center;
        gap: 20px;
        margin-top: 30px;
        // justify-content: center;

        p {
          font-size: 23px;
          color: #5e5d5d;
          font-weight: 300;
        }

        .settingsOfNumber {
          display: flex;
          align-items: center;
          justify-content: space-between;
          width: 150px;
          background-color: #fff;
          border: 1px solid #e0e0e0;


          .totlaNumberOfProduct {
            font-size: 22px;
          }

          button {
            font-size: 25px;
            font-weight: 800;
            background-color: #fff;
            border: none;
            padding: 4px 12px;
            cursor: pointer;

            &:first-child {
              border-right: 1px solid #e0e0e0;

            }

            &:last-child {
              border-left: 1px solid #e0e0e0;

            }

          }
        }
      }

      .buyProduct {
        margin-top: 33px;
        display: flex;
        align-items: center;
        gap: 30px;
        // justify-content: center;


        button {
          border: 0;
          padding: 10px 20px;
          font-size: 20px;
          font-weight: 300;
          // border-radius: 5px;
          cursor: pointer;
        }

        .addToCart {
          color: #ee3900;
          background-color: #ffc1adba;
          font-weight: 500;
          display: flex;
          align-items: center;
          gap: 10px;
          // border-radius: 0;
          border: 1px solid #b75959;
          transition: all .6s;

          &:hover {
            background-color: #ee3900;
            color: #fff;
          }
        }

        .cartIcone {
          font-size: 20px;
        }

        .buyNow {
          background-color: #ff5722;
          color: #fff;
          padding-top: 14px;
          padding-bottom: 14px;
        }
      }
    }
  }
}